/* some reset styles */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* general styles */

body {
  font-family: system-ui, 'Segoe UI', Roboto, Noto, Oxygen-Sans, Ubuntu,
    Cantrell, 'Helvetica Neue', sans-serif, 'Segoe UI Emoji', 'Segoe UI Symbol';
}

ul {
  list-style: none;
}

button {
  background-color: transparent;
  font-size: 16px;
  border: none;
  cursor: pointer;
}
